
import { computed, defineComponent, PropType, reactive, toRefs } from "vue";
import { useStore } from "vuex";
type ColItemStyle = string[] | object | string;
export interface ColumnProps {
	title: string;
	dataIndex?: string;
	render?: (text: any, rowData: object) => string | number;
	span?: number;
	slots?: { customRender: string };
	colItemStyle?: ColItemStyle;
}
export default defineComponent({
	props: {
		columns: {
			type: Array as PropType<ColumnProps[]>,
			default: [],
		},
		dataSource: {
			type: Array as PropType<Partial<{ id: number; name?: string }>[]>,
			default: [],
		},
		emptyText: {
			type: String,
			default: "暂无数据，请浏览其他歌单吧~",
		},
		colItemStyle: {
			type: [Object, String, Array] as PropType<ColItemStyle>,
			default: "",
		},
		startSpace: {
			type: Boolean,
			default: true,
		},
	},
	setup(props) {
		const store = useStore();
		const state = reactive({
			commonModalVisible: false,
			currentPlayList: computed(() => store.state.currentPlayList),
		});
		let clickedColumns = [];
		const clickMusicItem = (rowData) => {
			if (
				clickedColumns.length &&
				Date.now() - clickedColumns[clickedColumns.length - 1].clickTime < 400 &&
				rowData.id === clickedColumns[clickedColumns.length - 1].id
			) {
				if (rowData.haveUrl !== false) {
					store.commit("changeCurrentMusicDetail", rowData);
					store.commit("changeCurrentPlayList", JSON.parse(JSON.stringify(props.dataSource)));
					store.dispatch("getCurrentMusicUrlInfo", { id: rowData.id });
				} else {
					state.commonModalVisible = true;
				}
				return (clickedColumns = []);
			}
			rowData.clickTime = Date.now();
			clickedColumns.push(rowData);
		};
		const dealColItemStyle = (itemStyle) => {
			if (typeof itemStyle === "string" && typeof props.colItemStyle === "string") return props.colItemStyle;
			if (itemStyle instanceof Array && props.colItemStyle instanceof Array) return [...itemStyle, ...props.colItemStyle];
			if (itemStyle instanceof Object || props.colItemStyle instanceof Object) {
				let calculatedObj = {};
				if (itemStyle instanceof Object) {
					calculatedObj = itemStyle;
				}
				if (props.colItemStyle instanceof Object) {
					calculatedObj = props.colItemStyle;
				}
				if (itemStyle instanceof Object && props.colItemStyle instanceof Object) {
					calculatedObj = { ...itemStyle, ...props.colItemStyle };
				}
				return Object.fromEntries(Object.entries(calculatedObj).map(([key, value]) => [key, value + "!important"]));
			}
			return props.colItemStyle;
		};
		return {
			...toRefs(state),
			clickMusicItem,
			dealColItemStyle,
		};
	},
});
