
import { useRoute, useRouter } from "vue-router";
import { computed, defineComponent, PropType, reactive, toRefs, ComputedRef, watch, WritableComputedRef } from "vue";
export interface NavsProps {
	title: string | ComputedRef<string>;
	key?: number;
	active?: boolean;
	path?: string;
}
interface State {
	navList: WritableComputedRef<NavsProps[]>;
}
export default defineComponent({
	props: {
		navs: {
			type: Array as PropType<NavsProps[]>,
			default: [],
		},
	},
	emits: ["update:navs"],
	setup(props, { emit }) {
		const router = useRouter();
		const route = useRoute();
		const state = reactive<State>({
			navList: computed<NavsProps[]>({
				get: () => props.navs as NavsProps[],
				set: (newV) => emit("update:navs", newV),
			}),
		});
		const initActive = () => {
			if (state.navList.some((item) => route.path.endsWith(item.path))) {
				state.navList = state.navList.map((item) => {
					if (item.path && route.path.endsWith(item.path)) return { ...item, active: true };
					return { ...item, active: false };
				});
			}
		};
		initActive();
		//点击导航标题
		const clickNavItem = (nav, navIndex) => {
			state.navList = (state.navList as any[]).map((item, index) => {
				if (index == navIndex) {
					return {
						...item,
						active: true,
					};
				} else {
					return {
						...item,
						active: false,
					};
				}
			});
			if (nav.path) {
				router.push(nav.path);
			}
		};
		return {
			...toRefs(state),
			clickNavItem,
		};
	},
});
