
import { defineComponent, reactive, toRefs } from "vue";
export default defineComponent({
	emits: ["onClickAll", "onClickCat"],
	props: {
		categoryList: {
			type: Array,
			default: [],
		},
		nameDataIndex: {
			type: String,
			default: "name",
		},
		empty: {
			type: String,
			default: "全部",
		},
	},
	setup() {
		const state = reactive({
			categoryId: 0,
			category: null,
		});
		return {
			...toRefs(state),
		};
	},
});
